import React from 'react';
import style from './index.less';
import { transform } from '../../unit/const';
import { isMobile } from '../../unit';

export default class Guide extends React.Component {
  constructor() {
    super();
    this.state = {
      isMobile: isMobile(),
    };
  }
  shouldComponentUpdate() {
    return false;
  }
  render() {
    return (
      <div style={{ display: this.state.isMobile ? 'none' : 'block' }}>
        <div className={style.guide}>
          <div className={style.up}>
            <em style={{ [transform]: 'translate(0,-3px) scale(1,2)' }} /></div>
          <br />
          <div className={style.left}>
            <em style={{ [transform]: 'translate(-7px,3px) rotate(-90deg) scale(1,2)' }} />
          </div>
          <div className={style.down}>
            <em style={{ [transform]: 'translate(0,9px) rotate(180deg) scale(1,2)' }} /></div>
          <div className={style.right}>
            <em style={{ [transform]: 'translate(7px,3px)rotate(90deg) scale(1,2)' }} />
          </div>
        </div>
        <div className={`${style.guide} ${style.left}`}>
          <div className={style.space}>SPACE</div>
        </div>
        <a className={`${style.guide} ${style.link}`} href="http://www.atatech.org/articles/62566" rel="noopener noreferrer" target="_blank" title="用React+Redux+Immutable做 《俄罗斯方块》">关于“我”</a>
      </div>
    );
  }
}




